// Fonction de recherche RAG simple
export function searchKnowledge(query: string): string {
  const lowerQuery = query.toLowerCase();
  
  // Comptences
  if (lowerQuery.includes('comptence') || lowerQuery.includes('skill') || lowerQuery.includes('technologie') || lowerQuery.includes('langage')) {
    if (lowerQuery.includes('python')) {
      return "Maryam matrise Python  90%. Elle l'utilise pour le dveloppement backend avec Django, ainsi que pour l'IA/ML avec TensorFlow et Scikit-learn.";
    }
    if (lowerQuery.includes('javascript') || lowerQuery.includes('js') || lowerQuery.includes('react')) {
      return "Maryam matrise JavaScript  95%, notamment React.js (92%) pour le web et React Native (90%) pour le mobile. Elle utilise galement Node.js (90%) et Express.js (88%) pour le backend.";
    }
    if (lowerQuery.includes('mobile')) {
      return "Pour le dveloppement mobile, Maryam utilise React Native (90%), Flutter (85%), et Expo (85%). Elle a ralis plusieurs applications mobiles comme Elite Click et ChoWorks.";
    }
    if (lowerQuery.includes('ia') || lowerQuery.includes('ai') || lowerQuery.includes('machine learning') || lowerQuery.includes('ml')) {
      return "Les comptences de Maryam en IA/ML incluent TensorFlow (85%), Scikit-learn (88%), les Rseaux Neuronaux (80%), le NLP (75%), et l'OpenAI API (85%). Elle a cr FASTCUBE, une plateforme avec chatbot et recommandation intelligente.";
    }
    return `Maryam matrise ${portfolioKnowledge.skills.programmingLanguages.length} langages de programmation principaux dont Python (90%), JavaScript (95%), Java (85%), et C# (88%). Ses frameworks incluent React.js, React Native, Django, Node.js, .NET Core et Spring Boot.`;
  }
  
  // Projets
  if (lowerQuery.includes('projet') || lowerQuery.includes('project') || lowerQuery.includes('ralisation') || lowerQuery.includes('travail')) {
    if (lowerQuery.includes('elite') || lowerQuery.includes('publicit')) {
      return `Elite Click est le projet en cours de Maryam - une application mobile React Native pour digitaliser les services d'une agence de publicit  Beni Mellal. Technologies: React Native, TailwindCSS, Node.js, MySQL.`;
    }
    if (lowerQuery.includes('choworks')) {
      return `ChoWorks est une application mobile Flutter dveloppe par Maryam pour la gestion interne d'entreprise  Marrakech. Elle utilise Firebase pour l'authentification et la gestion temps rel. Projet termin en Aot-Sept 2024.`;
    }
    if (lowerQuery.includes('fastcube') || lowerQuery.includes('chatbot')) {
      return `FASTCUBE est une plateforme web avec IA dveloppe par Maryam. Elle intgre un chatbot conversationnel intelligent, un moteur de recommandation et un systme de rsum automatique grce  l'OpenAI API. Technologies: React.js, Node.js, Express.js, MySQL.`;
    }
    return `Maryam a ralis 7+ projets varis : Elite Click (mobile, en cours), ChoWorks (mobile Flutter), Verna Vert (desktop .NET), Renault CRM (desktop C#), FASTCUBE (web + IA), AMOUCH (web microservices), et TICKETS (web Django).`;
  }
  
  // Contact / Disponibilit
  if (lowerQuery.includes('contact') || lowerQuery.includes('email') || lowerQuery.includes('tlphone') || lowerQuery.includes('joindre')) {
    return `Vous pouvez contacter Maryam par email  maryam.fajri02@gmail.com ou par tlphone au +212 643776635. Elle rpond gnralement dans les 24 heures. Disponibilit: Lundi-Vendredi, 9h-18h.`;
  }
  
  if (lowerQuery.includes('disponible') || lowerQuery.includes('disponibilit') || lowerQuery.includes('available')) {
    return "Maryam est disponible immdiatement pour des projets freelance et des collaborations en remote. Son temps de rponse est gnralement de 24h. Elle travaille du lundi au vendredi, 9h-18h.";
  }
  
  // Services
  if (lowerQuery.includes('service') || lowerQuery.includes('proposer') || lowerQuery.includes('faire')) {
    return `Maryam propose plusieurs services : dveloppement d'applications mobiles (React Native, Flutter), dveloppement web (React.js, Node.js, Django), applications desktop (.NET, C#), intgration IA/ML, architecture microservices, et conception UI/UX responsive.`;
  }
  
  // Exprience
  if (lowerQuery.includes('exprience') || lowerQuery.includes('experience') || lowerQuery.includes('parcours')) {
    return `Maryam a 3+ ans d'exprience en dveloppement Full-Stack. Elle a ralis 7+ projets pour des clients au Maroc (Beni Mellal, Marrakech, Rabat, El Jadida), allant d'applications mobiles  des plateformes web avec IA.`;
  }
  
  // Tarifs / Collaboration
  if (lowerQuery.includes('tarif') || lowerQuery.includes('prix') || lowerQuery.includes('cot') || lowerQuery.includes('budget')) {
    return "Pour discuter des tarifs et du budget de votre projet, vous pouvez contacter Maryam directement par email (maryam.fajri02@gmail.com) ou via le formulaire de contact. Elle propose des devis personnaliss selon vos besoins.";
  }
  
  // Spcialisation
  if (lowerQuery.includes('spcialis') || lowerQuery.includes('expert') || lowerQuery.includes('fort')) {
    return "La spcialisation principale de Maryam est le dveloppement Full-Stack & IA/ML. Elle est particulirement forte en React Native/React.js (95%), Python (90%), Node.js (90%), et intgration d'IA avec OpenAI API (85%).";
  }
  
  // Rponse gnrale
  return `Maryam Fajri est une Ingnieure Full-Stack Freelance diplme en Informatique et Rseaux. Elle matrise 8+ langages de programmation et a ralis 7+ projets varis (mobile, web, desktop, IA). Elle est disponible en remote pour de nouveaux projets. N'hsitez pas  poser des questions spcifiques sur ses comptences, projets ou services !`;
}

